<template>
    <div class="grid md:grid-cols-2 gap-4">
        <!-- Box 1 -->
        <div class="shadow-md bg-green-100 p-5 text-center rounded">
            <h3 class="text-3xl text-green-900 font-bold mb-4">Cases</h3>

            <div class="text-2xl mb-4">
                <span class="font-bold">New:</span>
                {{ numberWithCommas(stats.NewConfirmed)}}
            </div>

            <div class="text-2xl mb-4">
                <span class="font-bold">Total:</span>
                {{ numberWithCommas(stats.TotalConfirmed) }}
            </div>
        </div>

        <!-- Box 2 -->
        <div class="shadow-md bg-green-200 p-5 text-center rounded">
            <h3 class="text-3xl text-green-900 font-bold mb-4">Deaths</h3>

            <div class="text-2xl mb-4">
                <span class="font-bold">New:</span>
                {{ numberWithCommas(stats.NewDeaths) }}
            </div>

            <div class="text-2xl mb-4">
                <span class="font-bold">Total:</span>
                {{ numberWithCommas(stats.TotalDeaths) }}
            </div>
        </div>
    </div>
</template>

<script setup>
defineProps({
    stats: Object
})

function numberWithCommas(n){
    return n.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
}
</script>